<template>
  <div class="geo">
    <ul class="geo__coordinates" v-if="coordinates.lat && coordinates.lng">
      <li class="geo__item">
        Lat:
        <span class="geo__result">{{ coordinates.lat }}</span>
      </li>
      <li class="geo__item">
        Lang:
        <span class="geo__result">{{ coordinates.lng }}</span>
      </li>
      <li class="geo__item">
        Accuracy:
        <span class="geo__result">{{ coordinates.accuracy.toFixed(1) }} m</span>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'GEO',

  data() {
    return {
      coordinates: {
        lat: null,
        lng: null,
        accuracy: null
      },
      geolocation: null,
    };
  },

  created() {
    this.geolocation = window.navigator.geolocation;
    this.geolocation.getCurrentPosition(this.setGeolocation);
    this.geolocation.watchPosition(this.setGeolocation);
  },

  methods: {
    setGeolocation(pos) {
      this.coordinates.lat = pos.coords.latitude;
      this.coordinates.lng = pos.coords.longitude;
      this.coordinates.accuracy = pos.coords.accuracy;
    }
  }
}
</script>

<style scoped>
  .geo__coordinates {
    margin: 0 20px 30px;
    padding: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: 16px;
    list-style: none;
  }

  .geo__result {
    font-size: 20px;
    color: rgb(247, 90, 90);
  }
</style>