<template>
  <div class="qr">
    <video ref="video" class="qr__video"></video>
    <div
      v-if="result"
      class="qr__result"
    >{{ result }}</div>
  </div>
</template>

<script>
import QrScanner from 'qr-scanner';

export default {
  name: 'QR',

  data() {
    return {
      qrScanner: null,
      result: null,
    };
  },

  mounted() {
    this.qrScanner = new QrScanner(this.$refs.video, result => this.result = result);

    this.qrScanner.start();
  },

  beforeUnmount() {
    if (this.qrScanner) this.qrScanner.stop();
  }
}
</script>

<style scoped>
  .qr {
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
  }

  .qr__video {
    width: 300px;
    height: 300px;
    object-fit: cover;
    margin: 0 0 30px;
  }

  .qr__result {
    line-height: 1;
    background: rgb(234, 207, 171);
    border: 2px solid rgb(195, 174, 147);
    text-align: center;
    width: 100%;
    padding: 5px;
    min-height: 30px;
    border-radius: 5px;
  }
</style>
