<template>
  <div class="app">
    <div class="wrap">
      <div class="btns">
        <BaseButton
          :class="isActiveClass('qr')"
          @click="changeType('qr')"
        >
          QR code
        </BaseButton>
        <BaseButton
          :class="isActiveClass('bc')"
          @click="changeType('bc')"
        >
          Bar code
        </BaseButton>
        <BaseButton
          variant="success"
          :class="isActiveClass('geo')"
          @click="changeType('geo')"
        >
          Geolocation
        </BaseButton>
      </div>
      <component
        v-if="type"
        :is="type.toUpperCase()"
      />
    </div>
  </div>
</template>

<script>
import '@/assets/index.css';
import QR from './components/QR'
import GEO from './components/GEO'
import BC from './components/BC'
import BaseButton from './components/base/BaseButton'

export default {
  name: 'App',

  components: {
    QR,
    BC,
    GEO,
    BaseButton,
  },

  data() {
    return {
      type: null,
    };
  },

  methods: {
    changeType (type) {
      this.type = type;
    },
    isActiveClass (type) {
      return type === this.type ? 'active' : null;
    }
  }
}
</script>

<style>
  .app {
    padding: 20px 0;
  }

  .wrap {
    max-width: 900px;
    margin: 0 auto;
    padding: 0 10px;
  }

  .btns {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 0 30px;
  }
</style>

