<template>
  <button
    class="btn"
    :class="`btn-${variant}`"
    @click="$emit('click')"
  >
    <slot>Default text</slot>
  </button>
</template>

<script>
export default {
  name: 'BaseButton',

  emits: ['click'],

  props: {
    variant: {
      type: String,
      default: 'primary',
      validator: (type) => ['primary', 'success'].includes(type),
    }
  }
}
</script>

